/** @format */

.hover-movie-card--container {
  cursor: pointer;
  width: 24rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  flex-direction: row;
  line-height: 1.2rem;
  justify-content: start;
  align-items: center;
  box-shadow: 0.4rem 0.4rem 0.8rem #0000001a;
  border-radius: 0.8rem;
  cursor: pointer;
  padding: 0.4rem;
}

.hover-movie-card--container h3 {
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 1.2rem;
}

.hover-movie-card--container p {
  font-size: 0.8rem;
  color: #6f6f6f;
  margin-bottom: 0.4rem;
}

.hover-movie-card-image {
  width: 12rem;
  border-radius: 0.4rem;
  align-self: center;
}

.price {
  margin-top: 1.8rem;
  font-weight: 700;
  font-size: 0.8rem !important;
}
