/** @format */

.search-input {
  height: 2.4rem;
  width: 32rem;
  margin-left: auto;
  margin-right: 1.8rem;
  border: 0.2rem solid #636363;
  border-radius: 0.4rem;
  display: flex;
  width: 100%;
}

.search-input input {
  font-size: 1.2rem;
  padding: 0rem 0.4rem;
  height: 100%;
  width: 100%;
  border: none;
  border-right: 0.1rem solid #6363635e;
  border-radius: 0.4rem 0rem 0rem 0.4rem;
}

input:focus {
  border: none;
  outline: none;
  border-right: 0.1rem solid #63636393;
  border-radius: 0.4rem 0rem 0rem 0.4rem;
}

.search-icon {
  border-radius: 0.4rem 0rem 0rem 0.4rem;
  width: 2.4rem;
  display: flex;
  justify-content: center;
}
