/** @format */

.sign-up--main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url(../../Image/SignUp.jpg);
  background-size: cover;
}

.sign-up--container {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  margin: auto;
  background-color: #f6f6f6;
  width: fit-content;
  padding: 2.4rem;
  border-radius: 0.8rem;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
  font-size: 1.2rem;
}

.sign-up--container h1 {
  text-align: center;
  margin: 0rem;
}

.sign-up--label {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  width: 22rem;
}

.sign-up--label input {
  height: 2.4rem;
  font-size: 1.2rem;
}

.sign-up--input-container {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.sign-up--button-container {
  display: flex;
  gap: 2.4rem;
}

.sign-up--button {
  width: 100%;
  height: 2.8rem;
  border-radius: 0.4rem;
  border: none;
  cursor: pointer;
}

.log-in {
  color: white;
  background-color: red;
}

.confirm {
  color: #f6f6f6;
  background-color: rgb(103, 103, 103);
}
