/** @format */

.movie-card--container {
  cursor: pointer;
  width: 10rem;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  line-height: 1.2rem;
  justify-content: center;
  align-items: center;
  height: 20rem;
}

.movie-card--container h3 {
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
}

.movie-card--container p {
  font-size: 0.8rem;
  text-align: center;
  color: #6f6f6f;
}

.card-image {
  width: 8.6rem;
  border-radius: 0.4rem;
  align-self: center;
}
