/** @format */
.header-container {
  display: flex;
  justify-content: space-between;
  padding: 0.4rem 2.4rem;
  align-items: center;
  background-color: #f6f6f6;
}

.logo-container {
  display: flex;
  gap: 1.2rem;
  align-items: center;
}

.logo {
  width: 3.6rem;
}

.search {
  cursor: pointer;
  width: 1.6rem;
}

.logo-text {
  font-size: 1.8rem;
}

.profile {
  background-color: #636363;
  border-radius: 100%;
  padding: 0.2rem;
  cursor: pointer;
}

.header-container--right-side {
  display: flex;
  gap: 2.4rem;
}
