/** @format */
.main-section {
  overflow-y: scroll;
  padding-bottom: 2.4rem;
}

.movie-container {
  scroll-behavior: smooth;
  padding: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.movie-container h2 {
  font-size: 1.6rem;
  font-weight: 500;
}

.main-container h2 {
  font-size: 1.8rem;
}

.movie-card--container-main {
  display: flex;
  flex-direction: row;
  overflow-x: hidden;
  gap: 2.4rem;
}

.hover-state {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
}
