/** @format */

.footer-container {
  width: 48rem;
  background-color: #f6f6f6;
  border-radius: 0.8rem 0.8rem 0rem 0rem;
  padding: 0rem 0.8rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  margin: auto;
  display: flex;
  justify-content: center;
  gap: 3.6rem;
}

.icon-container {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  font-size: 1.2rem;
  justify-content: center;
  align-items: center;
  width: 5.6rem;
  padding: 0.6rem 0rem 0.2rem 0rem;
  position: relative;
}

.navigation-active {
  background-color: #e40000;
  height: 0.4rem;
  width: 3.6rem;
  border-radius: 0rem 0rem 0.8rem 0.8rem;
  position: absolute;
  top: 0;
  display: block;
}

.active-text {
  color: #e40000;
  font-weight: 500;
}
