/** @format */
.edit-profile-main--container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(2px);
  position: absolute;
  top: 0;
  z-index: 1;
  background-color: rgba(160, 160, 160, 0.7);
}

.edit-profile-image {
  width: 2.4rem;
  height: 2.4rem;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
  cursor: pointer;
}

.edit-profile--container {
  height: 80vh;
  background-color: #f6f6f6;
  width: 26rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0.8rem;
  box-shadow: 0.4rem 0.4rem 0.8rem rgba(0, 0, 0, 0.1);
  position: relative;
}

.profile-picture {
  width: 6rem;
  height: 6rem;
  border-radius: 100%;
  background-color: #636363;
  position: relative;
  margin-bottom: 1.2rem;
}

.change-text {
  display: flex;
  gap: 1.8rem;
  flex-direction: column;
  margin: 4.6rem 0rem 3.2rem 0rem;
}

.change-text button {
  background-color: white;
  padding: 0.8rem;
  border-radius: 0.4rem;
  font-size: 1rem;
  cursor: pointer;
  width: 20rem;
  text-align: center;
  border: #6363633e solid;
}

.change-text--signout {
  background-color: red !important;
  color: white;
  width: 20rem;
  border-radius: 0.4rem;
  font-weight: 500;
  padding: 0.8rem;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  gap: 1.2rem;
  cursor: pointer;
}
